'use client';

import { useEffect, useState } from 'react';
import { cn } from '@/lib/utils';

interface TreeNodeData {
  label: string;
  children?: TreeNodeData[];
}

interface TreeNodeProps {
  node: TreeNodeData;
  depth: number;
  isAnimating: boolean;
  onActivate: () => void;
  animationDelay: number;
  isAnyChildActive?: boolean;
}

interface DecisionTreeProps {
  data: { root: TreeNodeData };
  isAnimating: boolean;
  delay: number;
}

const TreeNode: React.FC<TreeNodeProps> = ({ node, depth, isAnimating, onActivate, animationDelay }) => {
  const [isActive, setIsActive] = useState(false);
  const [areChildrenActive, setAreChildrenActive] = useState(false);
  const hasChildren = node.children && node.children.length > 0;

  useEffect(() => {
    let timer: NodeJS.Timeout;
    if (isAnimating) {
      timer = setTimeout(() => {
        setIsActive(true);
        onActivate();
      }, animationDelay);
    } else {
      setIsActive(false);
      setAreChildrenActive(false);
    }
    return () => clearTimeout(timer);
  }, [isAnimating, animationDelay, onActivate]);

  const handleChildActivation = () => {
    setAreChildrenActive(true);
  };

  return (
    <div className="flex flex-col items-center">
      <div
        className={cn(
          'decision-node relative bg-card border rounded-md px-2 py-1 text-xs shadow-sm transition-all duration-300',
          isActive && 'node-active'
        )}
        style={{
          transitionDelay: `${animationDelay}ms`,
        }}
      >
        {node.label}
      </div>
      {hasChildren && (
        <>
          <div
            className={cn(
              'line-down w-px bg-border transition-all duration-500',
              isActive || areChildrenActive ? 'h-4' : 'h-0'
            )}
            style={{ transitionDelay: `${animationDelay + 200}ms` }}
          />
          <div className="flex justify-center relative">
            <div
              className={cn(
                'line-across h-px bg-border absolute top-0 transition-all duration-500',
                isActive || areChildrenActive ? 'w-full' : 'w-0'
              )}
              style={{ transitionDelay: `${animationDelay + 300}ms` }}
            />
            {node.children?.map((child, index) => (
              <div key={index} className="flex flex-col items-center px-2">
                <div
                  className={cn(
                    'line-down w-px bg-border transition-all duration-500',
                    isActive || areChildrenActive ? 'h-4' : 'h-0'
                  )}
                  style={{ transitionDelay: `${animationDelay + 200}ms` }}
                />
                <TreeNode
                  node={child}
                  depth={depth + 1}
                  isAnimating={isAnimating && isActive}
                  onActivate={handleChildActivation}
                  animationDelay={500 + index * 500}
                />
              </div>
            ))}
          </div>
        </>
      )}
    </div>
  );
};

export const DecisionTree: React.FC<DecisionTreeProps> = ({ data, isAnimating, delay }) => {
    const [isActive, setIsActive] = useState(false);

    useEffect(() => {
        if (!isAnimating) {
            setIsActive(false);
        }
    }, [isAnimating])

  return (
    <div className="p-4 rounded-lg">
      <TreeNode 
        node={data.root} 
        depth={0} 
        isAnimating={isAnimating}
        onActivate={() => setIsActive(true)}
        animationDelay={1000 + delay}
      />
    </div>
  );
};
