'use client';

import { useEffect, useState } from 'react';
import Link from 'next/link';
import Image from 'next/image';
import { Button } from '@/components/ui/button';
import {
  Card,
  CardContent,
  CardHeader,
  CardTitle,
} from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { ArrowLeft, Sparkles, HeartPulse, X } from 'lucide-react';
import { DecisionTree } from '@/components/ui/decision-tree';
import { cn } from '@/lib/utils';
import placeholderImages from '@/lib/placeholder-images.json';

type Prediction = 'Risky' | 'Not Risky' | null;

export default function PredictPage() {
  const [bloodPressure, setBloodPressure] = useState('120/80');
  const [cholesterol, setCholesterol] = useState('200');
  const [heartRate, setHeartRate] = useState('75');
  const [bloodSugar, setBloodSugar] = useState('99');
  const [isMounted, setIsMounted] = useState(false);
  const [isPredicting, setIsPredicting] = useState(false);
  const [prediction, setPrediction] = useState<Prediction>(null);

  useEffect(() => {
    setIsMounted(true);
  }, []);

  const handlePredict = () => {
    setIsPredicting(true);
    const systolic = parseInt(bloodPressure.split('/')[0]);
    const chol = parseInt(cholesterol);
    const hr = parseInt(heartRate);
    const sugar = parseInt(bloodSugar);

    setTimeout(() => {
      let riskScore = 0;
      if (systolic > 130) riskScore++;
      if (chol > 220) riskScore++;
      if (hr > 85) riskScore++;
      if (sugar > 110) riskScore++;

      setPrediction(riskScore >= 2 ? 'Risky' : 'Not Risky');
    }, 4500); // Wait for animation to mostly complete
  };

  const resetPrediction = () => {
    setIsPredicting(false);
    setPrediction(null);
  };

  const allFieldsFilled =
    bloodPressure && cholesterol && heartRate && bloodSugar;

  if (!isMounted) {
    return null;
  }

  const treeData = {
    root: {
      label: 'BP > 130/85',
      children: [
        {
          label: 'Chol > 220',
          children: [
            { label: 'HR > 85', children: [{ label: 'Risky' }] },
            { label: 'Not Risky' },
          ],
        },
        {
          label: 'Sugar > 110',
          children: [{ label: 'Risky' }, { label: 'Not Risky' }],
        },
      ],
    },
  };

  const dataPoints = [
    { id: 'bp', top: '23%', left: '20%' },
    { id: 'chol', top: '23%', left: '80%' },
    { id: 'hr', top: '77%', left: '20%' },
    { id: 'bs', top: '77%', left: '80%' },
  ];

  return (
    <div className="flex flex-col min-h-screen bg-background overflow-hidden">
      <header className="sticky top-0 z-30 flex items-center h-16 px-4 border-b bg-background/80 backdrop-blur-sm">
        <Link href="/doctor">
          <Button variant="ghost" size="icon">
            <ArrowLeft className="w-5 h-5" />
          </Button>
        </Link>
        <h1 className="ml-4 text-xl font-semibold">
          Heart Attack Prediction
        </h1>
      </header>
      <main className="flex-1 p-4 md:p-10">
        <div className="max-w-xl mx-auto">
          <div className="text-center mb-8">
            <h2 className="text-3xl font-bold tracking-tight text-foreground">
              Patient Vitals Input
            </h2>
            <p className="mt-2 text-lg text-muted-foreground">
              Enter the patient's vitals to predict the risk of a heart attack.
            </p>
          </div>
          <div className="relative flex justify-center items-center" style={{minHeight: '450px'}}>
             <Image
              src={placeholderImages.human_body.src}
              alt="Human Body"
              width={450}
              height={338}
              data-ai-hint={placeholderImages.human_body['data-ai-hint']}
              className="object-contain"
            />
            
            {isPredicting && dataPoints.map(point => (
              <div key={point.id} className="absolute w-2 h-2 bg-primary rounded-full data-particle"
                   style={{ top: point.top, left: point.left, '--tree-num': Math.floor(Math.random() * 3) } as React.CSSProperties}
              />
            ))}

            <div className="absolute top-0 right-0">
              <Card className="w-36 shadow-lg transition-all duration-300 hover:shadow-primary/20 hover:shadow-xl">
                <CardHeader className='p-3'>
                  <CardTitle className='text-base'>Blood Pressure</CardTitle>
                </CardHeader>
                <CardContent className='p-3'>
                  <div className="grid w-full items-center gap-2">
                    <Input
                      id="bp"
                      value={bloodPressure}
                      onChange={(e) => setBloodPressure(e.target.value)}
                      placeholder="e.g. 120/80"
                      className="text-xs h-8"
                      disabled={isPredicting}
                    />
                  </div>
                </CardContent>
              </Card>
            </div>
            <div className="absolute top-0 left-0">
              <Card className="w-36 shadow-lg transition-all duration-300 hover:shadow-primary/20 hover:shadow-xl">
                <CardHeader className='p-3'>
                  <CardTitle className='text-base'>Cholesterol</CardTitle>
                </CardHeader>
                <CardContent className='p-3'>
                  <div className="grid w-full items-center gap-2">
                     <Input
                      id="chol"
                      value={cholesterol}
                      onChange={(e) => setCholesterol(e.target.value)}
                      placeholder="e.g. 200"
                      className="text-xs h-8"
                      disabled={isPredicting}
                    />
                  </div>
                </CardContent>
              </Card>
            </div>
            <div className="absolute bottom-0 left-0">
              <Card className="w-36 shadow-lg transition-all duration-300 hover:shadow-primary/20 hover:shadow-xl">
                <CardHeader className='p-3'>
                  <CardTitle className='text-base'>Heart Rate</CardTitle>
                </CardHeader>
                <CardContent className='p-3'>
                  <div className="grid w-full items-center gap-2">
                    <Input
                      id="hr"
                      value={heartRate}
                      onChange={(e) => setHeartRate(e.target.value)}
                      placeholder="e.g. 75"
                      className="text-xs h-8"
                      disabled={isPredicting}
                    />
                  </div>
                </CardContent>
              </Card>
            </div>
             <div className="absolute bottom-0 right-0">
              <Card className="w-36 shadow-lg transition-all duration-300 hover:shadow-primary/20 hover:shadow-xl">
                <CardHeader className='p-3'>
                  <CardTitle className='text-base'>Blood Sugar</CardTitle>
                </CardHeader>
                <CardContent className='p-3'>
                  <div className="grid w-full items-center gap-2">
                    <Input
                      id="bs"
                      value={bloodSugar}
                      onChange={(e) => setBloodSugar(e.target.value)}
                      placeholder="e.g. 99"
                      className="text-xs h-8"
                      disabled={isPredicting}
                    />
                  </div>
                </CardContent>
              </Card>
            </div>
          </div>
          {allFieldsFilled && !isPredicting && (
            <div className="flex justify-center mt-8">
              <Button size="lg" onClick={handlePredict}>
                <Sparkles className="w-5 h-5 mr-2" />
                Predict
              </Button>
            </div>
          )}
        </div>

        {isPredicting && (
           <div className="mt-12">
            <h3 className="text-2xl font-bold text-center mb-6 animate-fade-in">Random Forest in Action</h3>
             <div className="flex justify-around items-start animate-fade-in-up" style={{ animationDelay: '500ms'}}>
               <DecisionTree data={treeData} isAnimating={isPredicting} delay={0} />
               <DecisionTree data={treeData} isAnimating={isPredicting} delay={200} />
               <DecisionTree data={treeData} isAnimating={isPredicting} delay={400} />
             </div>
           </div>
        )}
      </main>

       {prediction && (
        <div className="fixed inset-0 bg-black/60 z-50 flex items-center justify-center animate-fade-in">
          <Card className={cn(
            "w-96 text-center transform transition-all animate-jump-in",
            prediction === 'Risky' ? 'border-destructive' : 'border-green-500'
          )}>
            <CardHeader>
              <CardTitle className="text-3xl">Prediction Result</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
               <div className={cn(
                  "mx-auto w-24 h-24 rounded-full flex items-center justify-center",
                  prediction === 'Risky' ? 'bg-destructive/20' : 'bg-green-500/20'
               )}>
                 <HeartPulse size={48} className={cn(prediction === 'Risky' ? 'text-destructive' : 'text-green-500')} />
               </div>
              <p className={cn(
                "text-4xl font-bold",
                prediction === 'Risky' ? 'text-destructive' : 'text-green-600'
              )}>
                {prediction}
              </p>
              <Button onClick={resetPrediction}>
                <X className="w-4 h-4 mr-2" />
                Close
              </Button>
            </CardContent>
          </Card>
        </div>
      )}
    </div>
  );
}